@font-face {
    font-family: 'Gilroy';
    src: url('../../public/Gilroy-ExtraBold.otf') format('woff2'),
         url('../../public/Gilroy-ExtraBold.otf') format('woff'),
         url('../../public/Gilroy-ExtraBold.otf') format('truetype');
    font-weight: bold;
    font-style: normal;
  }


.TradeMark_Container_Box {
    margin-left: 50px;
    width: 956px;
}

.Trademark_Column_Names {
    display: flex;
    border-bottom: #E7E6E6 solid 2px;
    margin-bottom: 20px;
}

.Trademark_Column_Names > h4 {
    margin-right: 150px;
    /* font-family: 'Gilroy', sans-serif; */
    color: #4B5563;
    font-weight: bold;
}

.Mark {
    margin-left: 40px;
}

.Search_Suggestions {
    width: 313px;
    height: 52px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
    margin-bottom: 10px;
}

.Search_Suggestions > h4 {
    height: 34.69px;
    align-content: center;
    /* font-family: 'Gilroy', sans-serif; */
    color: #4B5563;
}

.Search_Suggestions > button {
    width: 58px;
    height: 34.69px;
    border-radius: 10px;
    background-color: #FEF7F0;
    border: 1px solid #E7760E; 
    box-sizing: border-box;
}
