@font-face {
    font-family: 'Gilroy';
    src: url('../../public/Gilroy-Light.otf') format('woff2'),
         url('../../public/Gilroy-Light.otf') format('woff'),
         url('../../public/Gilroy-Light.otf') format('truetype');
    font-weight: bold;
    font-style: normal;
  }



.All_Filters_Container {
    width: 300px;
    margin-left: 50px;
}

.Filter_Share_Sort {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 30px;
}

.Filter_button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 95px;
    height: 42px;
    border-radius: 8px;
    background-color: white;
    align-content: center;
    border: 2px solid #D4D4D4; 
    box-sizing: border-box;
}

.Share_button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: white;
    border: 2px solid #D4D4D4; 
    box-sizing: border-box;
}

.Sort_button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: white;
    border: 2px solid #D4D4D4; 
    box-sizing: border-box;
}





.Status_Filter_Container {
    width: 300px;
    height: 180px;
    border-radius: 12px;
    margin-top: 60px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
}

.Status_Heading {
    height: 50px;
    margin-left: 20px;
    margin-top: 15px;
    display: flex;
    align-items: center;
}

.Status_Heading > h3 {
    margin-top: 15px;
    font-size: 16px;
}

.Status_Filter_Buttons {
    display: flex;
    flex-wrap: wrap;
    margin-left: 20px;
}

.Status_Filter_Buttons > button {
    /* height: 37px; */
    border-radius: 12px;
    margin-right: 10px;
    padding: 13px;
    font-size: 14px;
    margin-bottom: 10px;
    border: 2px solid #D4D4D4; 
    box-sizing: border-box;
    background-color: white;
}






.Owners_Filter_Container {
    height: 296px;
    width: 300px;
    border-radius: 12px;
    margin-top: 20px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
}

.Owners_Filters_Heading {
    display: flex;
    margin: 20px;
    margin-bottom: 0px;
}

.Owners_Filters_Heading > h4 {
    margin-right: 20px;
    font-size: 14px;
}

.Owners_Filter_Container > input {
    background-image: url('../../public/akar-icons_search.svg'); 
    background-size: 24px; 
    background-repeat: no-repeat; 
    background-position: 15px center; 
    background-color: #FCFCFE;
    padding-left: 45px;
    width: 256px;
    height: 40px;
    border-radius: 10px;
    margin-left: 20px;
    border: 2px solid #D4D4D4; 
    box-sizing: border-box;
}







.scrollable-container {
    width: 265px;
    height: 170px;
    overflow-y: auto;
    padding: 10px;
    border-radius: 8px;
    margin-left: 10px;
    margin-top: 10px;
}
  
.name-list {
    list-style-type: none;
    padding: 0;
    margin-left: 5px;
}
  
.name-list > li {
    margin: 10px 0;
    display: flex;
    align-items: center;
    font-family: 'Gilroy', sans-serif;
}

.name-list > li > input {
    font-weight: 100;
}
  
.name-list input[type="checkbox"] {
    width: 24px;
    height: 24px;
    margin-right: 20px; 
}






.Display_Filter_Container {
    height: 125px;
    width: 300px;
    border-radius: 12px;
    margin-top: 20px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
}

.Display_Filter_Container > h3 {
    margin: 20px;
    height: 50px;
    display: flex;
    align-items: center;
    margin-bottom: 0%;
}

.Display_Toggle {
    width: 260px;
    height: 50px;
    margin-left: 20px;
    margin-top: 0%;
    background-color: #F1F1F1;
    border-radius: 11px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.Grid_View {
    width: 120px;
    background-color: white;
    height: 35px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
}

.List_View {
    width: 120px;
    height: 35px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
}



  
  /* Style the scrollbar */
  .scrollable-container::-webkit-scrollbar {
    width: 8px; /* Width of the scrollbar */
  }
  
  /* Style the track (the area the thumb slides) */
  .scrollable-container::-webkit-scrollbar-track {
    background-color: white; /* Track color */
    border-radius: 10px; /* Optional: Adds rounded corners to the track */
  }
  
  /* Style the thumb (the draggable part) */
  .scrollable-container::-webkit-scrollbar-thumb {
    background-color: #D9D9D9; /* Color of the scrollbar thumb */
    border-radius: 10px; /* Optional: Adds rounded corners to the thumb */
  }
  
  /* Hover effect for the thumb */
  .scrollable-container::-webkit-scrollbar-thumb:hover {
    background-color: #555; /* Changes color on hover */
  }
  