.TradeMark_Container {
    display: flex;
    width: 900.44px;
    height: 120px;
    /* background-color: blue; */
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
}

.Image_Container {
    width: 158px;
    height: 120px;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
}

.Image_Container > img {
    width: 100%;
    height: 100%;
    border-radius: 15px;
}

.Details_Container {
    width: 102px;
    height: 108.68px;
    background-color: white;
}

.Status_Container {
    width: 141px;
    height: 106.85px;
    background-color: white;
}

.Description_Container {
    width: 296.43px;
    height: 83.32px;
    background-color: white;
}




.Details_Container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.Logo_Title_Container > h2 {
    font-size: 16px;
    font-weight: bold;
    color: #1A1A1A;
    margin: 0%;
}

.Logo_Title_Container > p {
    font-size: 14px;
    color: #1A1A1A;
    margin: 0%;
}

.ID_Date_Container > h2 {
    font-size: 16px;
    font-weight: bold;
    color: #1A1A1A;
    margin: 0%;
}

.ID_Date_Container > p {
    font-size: 14px;
    color: #1A1A1A;
    margin: 0%;
}






.Status_Container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.Status_Date {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.Status_Date > img {
    width: 8px;
    height: 8px;
}

.Status_Date > h2 {
    font-size: 16px;
    font-weight: bold;
    color: #41B65C;
    margin: 0%;
    margin-left: 5px;
}

.Status_Date_Container > p {
    font-size: 14px;
    color: #1A1A1A;
    margin: 0%;
}

.Second_Date_Container {
    display: flex;
}

.Second_Date_Container > p {
    font-size: 14px;
    color: #1A1A1A;
    margin: 0%;
    margin-left: 5px;
}






.Description_Container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.Description_Content > p {
    font-size: 14px;
    color: #1A1A1A;
    margin: 0%;
}

.Class_Images {
    display: flex;
}