@font-face {
    font-family: 'Gilroy';
    src: url('../../public/Gilroy-ExtraBold.otf') format('woff2'),
         url('../../public/Gilroy-ExtraBold.otf') format('woff'),
         url('../../public/Gilroy-ExtraBold.otf') format('truetype');
    font-weight: bold;
    font-style: normal;
  }


.Header {
    height: 118px;
    background-color: #F8FAFE;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-bottom: #EAF1FF solid 6px;
}

.Logo_Container {
    height: 21.93px;
    margin-left: 79.05px;
}

.Search_Container {
    display: flex;
    height: 59px;
    width: 591px;
    margin-left: 49.18px;
    justify-content: space-between;
    align-items: center;
}

.Search_Container > input {
    background-image: url('../../public/akar-icons_search.svg'); 
    background-size: 24px; 
    background-repeat: no-repeat; 
    background-position: 15px center; 
    padding-left: 45px; 
    height: 50px;
    width: 454.79px;
    border-radius: 12px;
    border: 1px solid #D4D4D4; 
    box-sizing: border-box;
    font-size: 14px;
}

.Search_Container > button {
    height: 50px;
    width: 124px;
    border-radius: 12px;
    background-color: #4380EC;
    color: white;
    font-weight: bold;
    font-family: 'Gilroy', sans-serif;
    font-size: 16px;
    border: 1px solid #D4D4D4; 
    box-sizing: border-box;
}
